import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TpCartaoRespostaLista } from '../cartao-resposta-lista';
import { TpCartaoRespostaItem } from '../../cartao-resposta-item/cartao-resposta-item';
import { TpCartaoRespostaItemType } from '../../cartao-resposta-item/utils/cartao-resposta-item.type';

const meta: Meta<
  TpCartaoRespostaItem & TpCartaoRespostaLista & TpCartaoRespostaItemType
> = {
  title: 'components/Core/Cart√£o resposta lista',
  argTypes: {
    flex: {
      control: { type: 'boolean' },
      description: 'Altera o layout do componente de grid para flex.',
      table: {
        defaultValue: { summary: false },
        type: { summary: 'boolean' },
      },
    },
  },
};

export default meta;

type Story = StoryObj<
  TpCartaoRespostaItem & TpCartaoRespostaLista & TpCartaoRespostaItemType
>;

export const Default: Story = {
  render: ({ ...args }) => {
    const generateAnswerCard = () => {
      let answerCard = [];

      answerCard = [
        { index: '01' },
        { index: '02', active: true },
        { index: '03', printed: true },
        { index: '04', canceled: true },
        { index: '05', incorrect: true },
        { index: '06', correct: true },
        { index: '07', discursive: true },
      ];

      for (let index = 8; index < 31; index++) {
        if (index < 10) {
          answerCard.push({
            index: '0' + index,
          });
        } else {
          answerCard.push({
            index: index,
          });
        }
      }

      return answerCard;
    };

    const items = generateAnswerCard();
    const itemTemplates = [];

    for (const item of items) {
      itemTemplates.push(
        html`<tp-cartao-resposta-item
          color=${args.color}
          ?active=${item.active}
          ?printed=${item.printed}
          ?canceled=${item.canceled}
          ?incorrect=${item.incorrect}
          ?correct=${item.correct}
          ?discursive=${item.discursive}
        >
          ${item.index}
        </tp-cartao-resposta-item>`,
      );
    }

    return html`
      <tp-cartao-resposta-lista ?flex=${args.flex}>
        ${itemTemplates}
      </tp-cartao-resposta-lista>
    `;
  },
};
